import React from "react"
import Meal from "./meal"

const Day =props=>{
    return (
        <div>
            {props.days.map((day, index)=>{
                return (
                <div key = {index} className = "box">
                    <h2>{day}</h2>
                        <Meal day = {day}/>
                </div>
                )
            })}
        </div>
        
    )
}

export default Day