import Meals from "./meals"
import Foods from "./Foods"

const Meal= props=>{
    const groups = Object.keys(Foods)

    return <div className = "day">

        {Meals.map(meal=>{
            return (

                <div 
                key = {"div"+props.day+meal}
                className = "meal"
                >

                    <h3>{meal}</h3>
                    {groups.map(group=>{

                        return <p 
                        id = {props.day+meal+group}
                        key = {props.day+meal+group}
                        >pick a {group}</p>
                    })}
                    <form>
                        <input 
                        type="text" 
                        placeholder="add ingredients"/>
                    </form>

                
                </div>
            )
        })}
    </div>

}
export default Meal