import React from "react"
import ListItems from "./listItems"



const ListFoodGroups = (props)=>{
   const keyList = Object.keys(props.list)
   
   return (
      <div>
      {keyList.map(group=>{
         return(
         <div 
         key = {"div"+group}
         className = "box"
         >
         <h2 key= {"h2"+group}>{group}</h2>
         <ListItems 
         list = {props.list}
         group = {group}
         newMeal = {props.newMeal}
         addToMealPlan = {props.addToMealPlan}
         addToShoppingList = {props.addToShoppingList}

         />
         </div>
         )
      })}
      </div>
   )
}
export default ListFoodGroups
